import React, {memo, useState, useEffect} from 'react';
import {Redirect, NavLink} from "react-router-dom";
import {useTranslation} from "react-i18next";
import $ from 'jquery';
import {SECRET_USER_DATA} from '../../../../services/Authentication/Types'
import {useDispatch, connect} from "react-redux";
import M from 'materialize-css'
import {FitData} from '../../../../entity/Redux/Auth/SignUp/Action';


const UploadFile = memo((props) => {
    const {t, i18n} = useTranslation();
    const dispatch = useDispatch();
    const [dataUser, setDataUser] = useState({isAssistance: false});

    useEffect(() => {


        $("input[name=Assistance]").change(function () {
            if ($(this).is(":checked")) {
                setDataUser({
                    ...dataUser,
                    isAssistance: true
                })
                $("#assistant-more-data").css({"display": "block"});
            } else {
                $("input[name=phoneNumber]").val('');
                $("textarea[name=description]").val('');
                setDataUser({
                    ...dataUser,
                    isAssistance: false
                })
                $("#assistant-more-data").css({"display": "none"});
            }
        })


        var elems = document.querySelectorAll('select');
        M.FormSelect.init(elems);

        if (props.respo) {
            if (props.respo.resLog.data) {
                if (props.respo.resLog.data.success) {

                    M.toast({html: 'please wait for redirect to dashboard ...'});
                    window.location.pathname = "/doctor";
                }
            }else {
                M.toast({html: 'your account already exist!'});
            }
        }
    })


    function handleLang(e) {
        e.preventDefault();
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", 'en');
        }
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === 'en') {
            localStorage.setItem("lan", 'ch');
        } else {
            localStorage.setItem("lan", 'en');
        }
        let lang = localStorage.getItem('lan');
        i18n.changeLanguage(lang);
    }

    function userData(e) {
        setDataUser({
            ...dataUser,
            [e.target.name]: e.target.value
        })
    }

    function submitUserDetail(e) {
        e.preventDefault();
        // var assisAttr = dataUser.isAssistance;
        // if (assisAttr) {
        //     setTimeout(() => {
        //     }, 1000)
        // } else {
        //     setDataUser({
        //         ...dataUser,
        //         phoneNumber : '',
        //         description : ''
        //     })
        //     setTimeout(() => {
        //         console.log("doc data : ", dataUser)
        //     }, 1000)
        // }


        dispatch(FitData(dataUser));
        // window.location.pathname = "/doctor"
    }


    const select_type = t('select-type.l');
    return (
        <div className="auth_back">
            <div id="back_shado"></div>

            <div className="container">
                <div className="col s12">
                    <div className="row">
                        <div className="col l4 m2 s1">

                        </div>
                        <div className="col l4 m8 s10">

                            <div id="auth_box">
                                <div id="change_or_sub">
                                    <input type="checkbox" name="cahngelan" onChange={handleLang}
                                           id="change_lang_login"/>
                                    <span>{t('Lang.l')}</span>
                                    {/*<span></span>*/}
                                </div>

                                <div className={`mini_box_over`}>
                                    <div id={`mini_circule`}>
                                        <i className="material-icons">person_outline</i>
                                    </div>
                                </div>
                                <div className={`mini_box_over`} style={{right: 0}}>
                                    <div id={`mini_circule_r`}>
                                        <img src="/img/icons/doctorB.png"/>
                                    </div>
                                </div>
                                <div className="container">
                                    <div className="row">

                                        <div className="row">
                                            <div className="input-field col s12" style={{marginTop: 55}}>
                                                <p id="text_sub">{t('Welcome-to.l')}</p>
                                                <p id="text_sub" style={{
                                                    fontSize: "40px",
                                                    marginTop: "15px !important",
                                                    marginBottom: "15px !important"
                                                }}>Bi Con</p>
                                                <p id="text_sub">{t('UserRegData.l')}</p>
                                            </div>
                                        </div>

                                        <div className="register_type_user" id="verifyBorder" style={{hight: "auto"}}>


                                            <div id="loading_back">


                                                <div className="preloader-wrapper big active" id="loading_progress">
                                                    <div className="spinner-layer spinner-blue-only">
                                                        <div className="circle-clipper left">
                                                            <div className="circle"></div>
                                                        </div>
                                                        <div className="gap-patch">
                                                            <div className="circle"></div>
                                                        </div>
                                                        <div className="circle-clipper right">
                                                            <div className="circle"></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                            <form onSubmit={e => submitUserDetail(e)}>

                                                <div className="container">
                                                    <div className="row">
                                                        <div className="col s12">
                                                            <div className="row">

                                                            </div>
                                                            <div className="file-field input-field">
                                                                <div className="btn"
                                                                     style={{backgroundColor: '#00b8d4 !important'}}>
                                                                    <span><i className="material-icons">attachment  </i></span>
                                                                    <input
                                                                        type="file"
                                                                        name="doc_file"/>
                                                                </div>
                                                                <div className="file-path-wrapper">
                                                                    <input className="file-path validate"
                                                                           type="text"
                                                                           placeholder="Upload one or more files"/>
                                                                </div>
                                                            </div>


                                                            <div className="col s6" style={{marginBottom: 20}}>
                                                                <p>
                                                                    <input name="Assistance"
                                                                           id="doctor-radio" className="with-gap"
                                                                           type="checkbox"
                                                                    />
                                                                    <label htmlFor="doctor-radio">Assistance</label>

                                                                </p>
                                                            </div>


                                                            <div id="assistant-more-data">

                                                                <div className="input-field col s12"
                                                                     style={{marginTop: 0}}>
                                                                    <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('phoneNumber.l')}</p>
                                                                    <div id="input_back">
                                                                        <input placeholder={t('U-phone-num.l')}
                                                                               name="phoneNumber"
                                                                               onChange={e => userData(e)}
                                                                               autoComplete="false"
                                                                               type="text" className="validate"/>
                                                                    </div>
                                                                    {/*<p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>*/}

                                                                    {/*<label htmlFor="password">Password</label>*/}
                                                                </div>

                                                                <div className="input-field col s12"
                                                                     style={{marginTop: 10}}>
                                                                    <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('ass-description.l')}</p>
                                                                    <div id="input_back">
                                                                            <textarea
                                                                                onChange={e => userData(e)}
                                                                                placeholder={t('U-ass-description.l')}
                                                                                name="description"
                                                                                autoComplete="false"
                                                                                className="validate"/>
                                                                    </div>

                                                                    {/*<label htmlFor="password">Password</label>*/}
                                                                </div>


                                                            </div>


                                                            <div className={`col s12`}
                                                                 style={{marginBottom: 30, marginTop: 30}}>
                                                                <input className="btn blue darken-4 waves-effect"
                                                                       type="submit"
                                                                       id="check_phone_num" value={t('Save.l')}/>
                                                                {/*</Link>*/}
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>


                                            </form>

                                        </div>
                                    </div>
                                </div>

                            </div>


                        </div>
                        <div className="col l4 m2 s1">

                        </div>
                    </div>

                </div>

            </div>


        </div>
    );
})

const mapStateToProps = (state) => {
    return {respo: state.Auth}
}
export default connect(mapStateToProps, {FitData})(UploadFile);